.App {
  height: 100vh;
  width: 100vw;
  display: flex;
}

/*Navbar position button*/

.btn-position{
  width: 20%;
  display: flex;
  contain: content;
  flex-direction: column;
  background-color: rgb(202, 155, 155, 0.6);
  border: 1px solid black;
}

.grp-name{
  height:20%;
  text-align: center;
  /* justify-content: center; */
  contain: content;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: small;
  background-color: rgb(241, 230, 230);
}

/*Direction button Css*/

.btn{
  height: 20%;
  width: 100%;
  border: 3px solid grey;
  border-radius: 10px;
  background: rgba(255, 255, 255,0.2);
}

/*Hover button container*/

.button-container{
  display: block;
  max-height: max-content;
  width: 80%;
  text-align: center;
  border: 2px solid green;
}

.hoverContainer{
  margin-top: 27%; 
}

/*Hover button*/

.hover-btn{
  display: inline-block;
  height: 20%;
  width: 30%;
  font-size: 6rem;
}

/*Top position hover*/

.up{
  height: min-content;
  width: 50%;
  margin-top: -10%;
  margin-left: 25%;
  contain: content;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1.5rem;
  background-color: rgb(241, 218, 218, 0.9);
  
}

/*Right position hover*/

.right{
    height: min-content;
    width: 30%;
    margin-top:-5%;
    margin-left: 60%;
    contain: content;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1.5rem;
    background-color: rgb(241, 218, 218, 0.9);
}

/*Down position hover*/

.down{
  height: min-content;
  width: 50%;
  margin-top: 5%;
  margin-left: 25%;
  contain: content;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1.5rem;
  background-color: rgb(241, 218, 218, 0.9);
}

/*Left position hover*/

.left{
  height:min-content;
  width: 30%;
  margin-top: -5%;
  margin-left: 8%;
  contain: content;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1.5rem;
  background-color: rgb(241, 218, 218, 0.9);
}
.active{
  background-color:rgba(104, 23, 227, 0.3) ;
}
